<template>
  <el-main>
    <h3 style="padding-bottom: 5px;border-bottom: 1px solid #eee">企业名称：{{companyName}}</h3>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人姓名：</i></label>
      <el-input class="input-width" v-model="servesInfo.userName" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人性别：</i></label>
      <el-select v-model="servesInfo.sex" placeholder="请选择">
        <el-option
          v-for="item in sexList"
          :key="item.dValue"
          :label="item.dValue"
          :value="item.dKey">
        </el-option>
      </el-select>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人证件类型：</i></label>
      <el-select v-model="servesInfo.paperType" placeholder="全部">
        <el-option
          v-for="item in paperTypeList"
          :key="item.dKey"
          :label="item.dValue"
          :value="item.dKey">
        </el-option>
      </el-select>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人证件号码：</i></label>
      <el-input class="input-width" v-model="servesInfo.paper" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>被服务人电话号码：</i></label>
      <el-input class="input-width" v-model="servesInfo.mobile" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item"><i>被服务人电子邮件：</i></label>
      <el-input class="input-width" v-model="servesInfo.email"  placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>所属商品：</i></label>
      <el-select v-model="servesInfo.ptGoodsId" placeholder="全部">
        <el-option
          v-for="item in goodsInfoList"
          :key="item.id"
          :label="item.name"
          :value="item.id">
        </el-option>
      </el-select>
    </div>
    <div class="list">
      <label for="" class="list-item require"><i>保单号/服务单号：</i></label>
      <el-input class="input-width" v-model="servesInfo.cardNum" placeholder="请输入内容"></el-input>
    </div>
    <div class="list"> 
        <label for="" class="list-item require"><i>商品生效日期：</i></label>
        <el-date-picker v-model="servesInfo.beginTime" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" />
        
        <label for="" class="list-item require"><i>商品失效日期：</i></label>
        <el-date-picker v-model="servesInfo.endTime" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" />
      </div>
    <div class="list">
      <label class="list-item"><i>特别约定：</i></label>
        <el-input
          type="textarea"
          :rows="2"
          size="medium"
          placeholder="请输入内容"
          v-model="servesInfo.remark">
        </el-input>
    </div>
    <div class="list">
      <label class="list-item textarea-label"><i>保单状态：</i></label>
      <div class="list-radio">
        <el-radio v-model="servesInfo.status" label="0">正常</el-radio>
        <el-radio v-model="servesInfo.status" label="1">中止</el-radio>
        <el-radio v-model="servesInfo.status" label="2">终止</el-radio>
        <el-radio v-model="servesInfo.status" label="3">失效</el-radio>
      </div>
    </div>
    <div class="list btn-center">
      <el-button type="primary" @click='addOrderRelation'>增加关联权益人</el-button>
    </div>
    <div class="other-info" v-show="otherInfo">
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人姓名：</i></label>
        <el-input class="input-width" v-model="orderRelation.userName" placeholder="请输入内容"></el-input>
      </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人性别：</i></label>
        <el-select v-model="orderRelation.sex" placeholder="全部">
          <el-option
            v-for="item in sexList"
            :key="item.dValue"
            :label="item.dValue"
            :value="item.dKey">
          </el-option>
        </el-select>
      </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人证件类型：</i></label>
        <el-select v-model="orderRelation.paperType" placeholder="全部">
          <el-option
          v-for="item in paperTypeList"
          :key="item.dKey"
          :label="item.dValue"
          :value="item.dKey">
        </el-option>
        </el-select>
      </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人证件号码：</i></label>
        <el-input class="input-width" v-model="orderRelation.paper" placeholder="请输入内容"></el-input>
    </div>
      <div class="list">
        <label for="" class="list-item require"><i>关联权益人手机号码：</i></label>
        <el-input class="input-width" v-model="orderRelation.mobile" placeholder="请输入内容"></el-input>
    </div>
      <div class="list">
        <label for="" class="list-item"><i>关联权益人电子邮件：</i></label>
        <el-input class="input-width" v-model="orderRelation.email" placeholder="请输入内容"></el-input>
    </div>
    <div class="list">
      <label class="list-item textarea-label"><i>与被服务人关系：</i></label>
      <div class="list-radio">
        <el-radio v-model="orderRelation.relationType" label="1">父母</el-radio>
        <el-radio v-model="orderRelation.relationType" label="2">子女</el-radio>
        <el-radio v-model="orderRelation.relationType" label="3">配偶</el-radio>
        <el-radio v-model="orderRelation.relationType" label="4">其他</el-radio>
      </div>
    </div>
    </div>
    <div class="btn-center">
      <el-button type="primary" @click='confirm'>提交</el-button>
      <el-button type="" @click='cancel'>取消</el-button>
    </div>
  </el-main>
</template>

<script>
const queryString = require('querystring')
import { servesInfo } from 'data/servesParams'
import { sexList, paperTypeList } from 'data/listBody'
export default {
  name: 'addOrEditServe',
  data() {
    return {
      companyName: '',
      sexList,
      paperTypeList,
      goodPlanList: [],
      otherInfo: false,
      servesInfo: JSON.parse(JSON.stringify(servesInfo)),
      goodsInfoList: [],
      orderRelation: {}
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      const query = this.$route.query
      this.companyName = query.companyName
      this.companyId = query.companyId
      this.isEdit = query.type === 'edit'
      this.goodsName = this.isEdit ? query.goodsName : "新增商品"
      this.servesInfo.companyId = this.companyId
      this.getGoodsList()
      if (query.id) {
          this.servesInfo.id = query.id
          this.getEditServe()
        } else {
          delete this.servesInfo.id
        }
    },
    getGoodsList() {
      const companyId = this.companyId
      const params = {
        companyId,
        page: 0,
        size: 1000
      }
      
      this.axios.get('/goods?' + queryString.stringify(params)).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.goodsInfoList = res.data.data.list
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    },
    addOrderRelation() {
      this.otherInfo = true;
    },
    confirm() {
      if (this.otherInfo) {
        this.servesInfo.orderRelation = this.orderRelation
      } else {
        delete this.servesInfo.orderRelation
      }
      this.servesInfo.status = Number(this.servesInfo.status)
      this.servesInfo.ptGoodsId = Number(this.servesInfo.ptGoodsId)
      this.servesInfo.companyId = Number(this.servesInfo.companyId)
      this.servesInfo.sex = Number(this.servesInfo.sex)
      const type = this.edit ? 'put' : 'post'
      this.axios[type]('/order', this.servesInfo).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.$message(this.mes.success)
          this.$router.back()
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    },
    cancel() {
      this.$router.back()
    },
    getGoodsPlan() {
      const param = {
        companyId: this.companyId
      }
      this.axios.get('/goodsPlan?' +  queryString.stringify(param)).then(res => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.goodPlanList = res.data.data
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    },
    getEditServe() {
      this.axios.get(`/order/${this.servesInfo.id}`).then(res => {
        const code = Number(res.data.code)
        if (code === 200) {
          const data = res.data.data
          for (var key in servesInfo) {
            this.servesInfo[key] = data[key]
          }
          this.servesInfo.beginTime = this.timeFormater(data.beginTime).format('YYYY-MM-DD')
          this.servesInfo.endTime = this.timeFormater(data.endTime).format('YYYY-MM-DD')
          this.servesInfo.status = String(this.servesInfo.status)
          this.otherInfo = !!this.servesInfo.orderRelation
          this.orderRelation = this.servesInfo.orderRelation || {}
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import url('../../../assets/less/input.less');
